import React, { useContext, useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import CloudUploadIcon from "@material-ui/icons/CloudUpload";
import { GrobalContex } from "../../contexts/GlobalContex";
import LinearProgress from "@material-ui/core/LinearProgress";
//cookes manager
import cookie from "js-cookie";

import axios from "axios";

const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(1),
    },
  },
}));

var currencies = [
  {
    name: "Collage",
    department: ["Choose A Colage"],
  },
  {
    name: "IEM",
    department: ["IEMCSE", "IEMECE", "IEMBBA", "IEMBA", "IEMHT", "IEMBCA"],
  },
  {
    name: "UEM",
    department: ["UEMCSE", "UEMECE", "UEMMATH", "UEMPHYSIC", "UEMBCA"],
  },
  {
    name: "AOT",
    department: ["AOTOIU", "AOTFER", "AOTLPO", "AOTPOU", "AOTMNV", "AOTBBJ"],
  },
  {
    name: "MCKV",
    department: ["MCKVMKO", "MCKVNJI", "MCKVYGV", "MCKVCFT", "MCKVXDR"],
  },
];

/// upload form system ///
export default function Uploadform() {
  const classes = useStyles();

  const [Progressbar, setProgressbar] = useState(false)

  const [InstituteList, setInstituteList] = useState(currencies);
  const [Issetfile, setIssetfile] = useState({
    fileState: false,
    name: "",
  });

  const [Values, setValues] = useState({
    institute: "Collage",
    department: "Choose A Colage",

    topic_name: "",
    subject_name: "",
    level: "",
    field: "",
    singlenote: "",
  });
  const [Index, setIndex] = useState(0);

  const [GlobalContexValue, setGlobalContexValue] = useContext(GrobalContex);

  useEffect(() => {
    console.log("Index");

    console.log(
      InstituteList.findIndex((item) => item.name === Values.institute)
    );
    let indx = InstituteList.findIndex(
      (item) => item.name === Values.institute
    );
    setIndex(indx);
    console.log(indx);
  }, [Values]);

  const handleChange = (prop) => (event) => {
    console.log("handleChange");
    setValues({ ...Values, [prop]: event.target.value });
    console.log(JSON.stringify(Values));
  };

  const handleChangeFile = (prop) => (event) => {
    setValues({ ...Values, [prop]: event.target.files[0] });

    console.log(" | 57 Debug event.target.files | " + event.target.files[0]);
    console.log(JSON.stringify(Values));
    console.log(JSON.stringify(event.target.files[0].name));
    console.log(JSON.stringify(Values.singlenote));

    setIssetfile({
      ...Issetfile,
      fileState: true,
      name: JSON.stringify(event.target.files[0].name),
    });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    setProgressbar(true)

    console.log("handleSubmit");

    const formData = new FormData();

    formData.append("enctype", "multipart/form-data");
    formData.append("institute", Values.institute);
    formData.append("topic_name", Values.topic_name);
    formData.append("subject_name", Values.subject_name);

    formData.append("level", Values.level);
    formData.append("field", Values.field);
    formData.append("department", Values.department);
    formData.append("singlenote", Values.singlenote);

    console.log(formData);

    axios({
      // `url` is the server URL that will be used for the request
      url: "/api/notes/users-upload-single-notes",

      // `method` is the request method to be used when making the request
      method: "post",

      // `baseURL` will be prepended to `url` unless `url` is absolute.
      baseURL: GlobalContexValue.url,

      // `headers` are custom headers to be sent
      headers: {
        "X-Requested-With": "XMLHttpRequest",
        Authorization: "Bearer" + " " + cookie.get("access"),
      },

      // `data` is the data to be sent as the request body
      data: formData,
    })
      .then((result) => {
        console.log(result);
        setProgressbar(false)
        
        window.location.reload();
        alert("Your Notes Is Succefull Uploaed")

      })
      .catch((error) => {
        console.log(error);
        setProgressbar(false)
        window.location.reload();

      })
  };

  //  desplay part   ///
  return (
    <form
      className={classes.root}
      noValidate
      autoComplete="off"
      onSubmit={handleSubmit}
    >
      <TextField
        id="outlined-select-currency-native"
        select
        required
        label="institute"
        name="institute"
        onChange={handleChange("institute")}
        style={{ width: "230px" }}
        SelectProps={{
          native: true,
        }}
        variant="outlined"
      >
        {InstituteList.map((option) => (
          <option key={option.name} value={option.name}>
            {option.name}
          </option>
        ))}
      </TextField>

      <TextField
        id="outlined-select-currency-native"
        select
        required
        label="department"
        name="department"
        variant="outlined"
        onChange={handleChange("department")}
        style={{ width: "230px" }}
        SelectProps={{
          native: true,
        }}
      >
        {InstituteList[Index].department.map((option) => (
          <option key={option} value={option}>
            {/* {option.department}  */}
            {option}
          </option>
        ))}
      </TextField>

      <TextField
        required
        id="outlined-basic"
        label="topic name"
        variant="outlined"
        name="topic_name"
        onChange={handleChange("topic_name")}
      />
      <TextField
        required
        id="outlined-basic"
        label="Subject Name"
        variant="outlined"
        name="subject_name"
        onChange={handleChange("subject_name")}
      />
      <TextField
        required
        id="outlined-basic"
        label="Level"
        variant="outlined"
        name="level"
        onChange={handleChange("level")}
      />
      <TextField
        required
        id="outlined-basic"
        label="field"
        variant="outlined"
        name="field"
        onChange={handleChange("field")}
      />
      <div className={classes.root}>
        <span> {Issetfile.fileState ? "File : " + Issetfile.name : ""}</span>
      </div>

      <div className={classes.root}>
        {Progressbar ? <LinearProgress color="secondary"  /> : "" }
        
      </div>

      <input
        // multiple
        required
        accept="image/*"
        className={classes.input}
        id="contained-button-file"
        name="singlenote"
        type="file"
        style={{ display: "none" }}
        onChange={handleChangeFile("singlenote")}
      />

      <label htmlFor="contained-button-file">
        <Button
          variant="contained"
          color="inherit"
          component="span"
          startIcon={<CloudUploadIcon />}
          size="large"
        >
          select file to upload
        </Button>
      </label>

      <Button variant="contained" color="secondary" size="large" type="submit">
        Submmit
      </Button>
    </form>
  );
}
